<template>
	<view class="content">
		<uni-list>
			<uni-list-item title="上报人" :rightText="detail.reportPerson"></uni-list-item>
			<uni-list-item title="上报时间" :rightText="detail.reportTime"></uni-list-item>
			<uni-list-item title="隐患类型" :rightText="detail.eventType"></uni-list-item>
			<uni-list-item title="设备设施类别" :rightText="detail.devType"></uni-list-item>
			<uni-list-item title="隐患详情" :rightText="detail.eventDetail"></uni-list-item>
			<uni-list-item title="隐患地点" :rightText="detail.eventLocation" link @click="openLocation"></uni-list-item>
			<uni-list-item title="现场图片">
				<view slot="footer">
					<view style="width:480rpx;text-align: right;">
						<image class="image-style" :src="formatimageUrl(item.picUrl)" v-for="item in detail.picUrlList" :key="item.id"></image>
					</view>
				</view>
			</uni-list-item>
		</uni-list>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			detail: {},
			eventId: ''
		};
	},
	onLoad(options) {
		this.eventId = options.eventId;
	},
	onShow() {
		if (this.eventId) {
			this.getEventByEventId(this.eventId);
		}
	},
	methods: {
		getEventByEventId(eventId) {
			taskAPI.getEventByEventId({ eventId }).then(res => {
				if (res.code == 200) {
					this.detail = res.data;
				}
			});
		},
		formatimageUrl(url) {
			return process.uniEnv.FILE_URL + url;
		},
		openLocation(){
			console.log(this.detail.shape)
			let temp = this.detail.shape.split('POINT(')[1]
			const latitude =temp.split(' ')[1].split(')')[0];
			const longitude = temp.split(' ')[0];
			console.log(latitude,longitude)
			uni.openLocation({
				latitude: Number(latitude),
				longitude: Number(longitude),
				success: function () {
					console.log('success');
				}
			});
		}
	}
};
</script>

<style></style>
